const logoLight = document.querySelector('.logo__img-light');
const logoDark = document.querySelector('.logo__img-dark');

const headerLight = document.querySelector('.header').classList.contains('header_light');

const elementBurger = document.querySelector('.burger');
const classNameActive = 'active';

const elementHeader = document.querySelector('.header');
const classNameHeaderLight = 'header_light';


function observeClass(elementBurger, classNameActive, elementHeader, classNameHeaderLight) {
   const observer = new MutationObserver((mutations) => {
      const burgerActive = elementBurger.classList.contains(classNameActive);
      const headerLight = elementHeader.classList.contains(classNameHeaderLight);
      
      // console.log(`Class ${classNameActive} is now: ${burgerActive}`);
      // console.log(`Class ${classNameHeaderLight} is now: ${headerLight}`);
 
      if (burgerActive && !headerLight) {
         logoLight.classList.add('logo__img_hidden');
         logoDark.classList.remove('logo__img_hidden');
      } else if (!burgerActive && !headerLight) {
         logoLight.classList.remove('logo__img_hidden');
         logoDark.classList.add('logo__img_hidden');
      }
   });
 
   observer.observe(elementBurger, { 
      attributes: true, 
      attributeFilter: ['class'] 
   });
}
 
observeClass(elementBurger, classNameActive, elementHeader, classNameHeaderLight);